    <div class="tod-container">
      <div v-if="showIntro" class="tod-intro">
        <h1 class="tod-title">–ü—Ä–∞–≤–¥–∞ –∏–ª–∏ –¥–µ–π—Å—Ç–≤–∏–µ</h1>
        <p class="tod-subtitle">
          –ö–ª–∞—Å—Å–∏—á–µ—Å–∫–∞—è –∏–≥—Ä–∞ –¥–ª—è –≤–µ—á–µ—Ä–∏–Ω–æ–∫, —Å–≤–∏–¥–∞–Ω–∏–π –∏ –¥–æ–ª–≥–∏—Ö —Ä–∞–∑–≥–æ–≤–æ—Ä–æ–≤. –ù–µ–π—Ä–æ–Ω–Ω—ã–π –í–æ–ª–∫ –∑–∞–¥–∞—ë—Ç –≤–æ–ø—Ä–æ—Å—ã –∑–∞ –≤–∞—Å.
        </p>
        <button class="tod-btn tod-btn-primary" @click="startGame">–ò–≥—Ä–∞—Ç—å –æ–Ω–ª–∞–π–Ω</button>
        <p class="tod-hint">–ù–∞–∂–º–∏—Ç–µ, —á—Ç–æ–±—ã —Å—Ä–∞–∑—É –ø–µ—Ä–µ–π—Ç–∏ –∫ –ø–µ—Ä–≤–æ–º—É –≤–æ–ø—Ä–æ—Å—É</p>
      </div>

      <div v-else class="tod-game">
        <div class="tod-modes">
          <button
            v-for="m in modes"
            :key="m.id"
            class="tod-chip"
            :class="{ 'tod-chip-active': mode === m.id }"
            @click="() => { mode = m.id; askRandom(); }"
          >
            {{ m.label }}
          </button>
        </div>

        <div class="tod-card">
          <div class="tod-label">
            <span>{{ currentType === 'truth' ? '–ø—Ä–∞–≤–¥–∞' : '–¥–µ–π—Å—Ç–≤–∏–µ' }}</span>,
            <span>{{ currentModeLabel }}</span>
          </div>
          <div class="tod-question-text" :class="{ 'tod-question-animate': isAnimating }">
            {{ currentQuestion }}
          </div>
          <p class="tod-wolf-hint">
            –ø–æ–¥—Å–∫–∞–∑–∫–∞ –æ—Ç –≤–æ–ª–∫–∞:
            <span class="tod-wolf-hint-strong">{{ currentHint }}</span>
          </p>
        </div>

        <div class="tod-actions">
          <div class="tod-buttons">
            <button class="tod-btn tod-btn-truth" @click="askTruth">–ü—Ä–∞–≤–¥–∞</button>
            <button class="tod-btn tod-btn-ghost" @click="askRandom">–°–ª—É—á–∞–π–Ω–æ</button>
            <button class="tod-btn tod-btn-dare" @click="askDare">–î–µ–π—Å—Ç–≤–∏–µ</button>
          </div>
        </div>
      </div>
    </div>
    <SocialPopup :visible="showPopup" :payload="popupPayload" @close="showPopup = false" />
  </section>
