<script setup>
import { computed, ref } from 'vue';
import SocialPopup from '@/components/ui/SocialPopup.vue';
import { modes, questions } from './questions';

const showIntro = ref(true);
const mode = ref('classic');
const currentType = ref('truth');
const currentQuestion = ref('');
const currentHint = ref('');
const isAnimating = ref(false);
const lastIndex = ref({ truth: -1, dare: -1 });
const questionsSeen = ref(0);
const showPopup = ref(false);
const popupIndex = ref(0);

const hints = {
  truth: [
    '–û—Ç–≤–µ—á–∞–π —á–µ—Å—Ç–Ω–æ, –≤–æ–ª–∫ –≤—Å—ë —Ä–∞–≤–Ω–æ —á—É–≤—Å—Ç–≤—É–µ—Ç –ª–æ–∂—å.',
    '–ù–µ –±–æ–π—Å—è —Ä–∞—Å–∫—Ä—ã—Ç—å—Å—è ‚Äî —á–µ–º –∏—Å–∫—Ä–µ–Ω–Ω–µ–π, —Ç–µ–º –∏–Ω—Ç–µ—Ä–µ—Å–Ω–µ–π.',
    '–ö–∞–∂–¥—ã–π —á–µ—Å—Ç–Ω—ã–π –æ—Ç–≤–µ—Ç ‚Äî –Ω–æ–≤—ã–π –ø–æ–≤–æ–¥ –¥–ª—è —Ä–∞–∑–≥–æ–≤–æ—Ä–∞.'
  ],
  dare: [
    '–î–µ–π—Å—Ç–≤—É–π —Å–º–µ–ª–æ, –Ω–æ –±–µ–∑ –Ω–µ–ø—Ä–∏—è—Ç–Ω–æ—Å—Ç–µ–π.',
    '–í—ã–±–∏—Ä–∞–π —Ç–æ, —á—Ç–æ —Ä–µ–∞–ª—å–Ω–æ —Å–¥–µ–ª–∞—Ç—å –ø—Ä—è–º–æ —Å–µ–π—á–∞—Å.',
    '–ü—É—Å—Ç—å –∑–∞–¥–∞–Ω–∏–µ —Ä–∞–¥—É–µ—Ç –≤—Å–µ—Ö, –∞ –Ω–µ —Ç–æ–ª—å–∫–æ –≤–æ–ª–∫–∞.'
  ]
};

const socials = [
  {
    title: '–ü–æ–¥–ø–∏—à–∏—Å—å –Ω–∞ Telegram',
    text: '–ö—É—á–∞ –º–µ–º–æ–≤, –≤—Å—ë —Å–∞–º–æ–µ —Å–≤–µ–∂–µ–µ —Ç—É—Ç',
    cta: '–ü–µ—Ä–µ–π—Ç–∏ –≤ –ª–æ–≥–æ–≤–æ',
    link: 'https://t.me/neural_wise_wolf',
    emoji: 'üì¨'
  },
  {
    title: '–ó–∞–ª–µ—Ç–∞–π –≤ Instagram',
    text: '–°–∞–º–æ–µ –ø–µ—Ä–≤–æ–µ –∏ –±–æ–ª—å—à–æ–µ —Å–æ–æ–±—â–µ—Å—Ç–≤–æ, –º–Ω–æ–≥–æ –º–µ–º–æ–≤ —Å –≤–æ–ª–∫–∞–º–∏',
    cta: '–û—Ç–∫—Ä—ã—Ç—å Instagram',
    link: 'https://instagram.com/neural_wise_wolf/',
    emoji: 'üì∏'
  },
  {
    title: 'TikTok –í–æ–ª–∫–∞',
    text: '–ú–µ–º—ã, —Å—Ç—Ä–∏–º—ã –∏ –º–Ω–æ–≥–æ –≤–æ–ª–∫–æ–≤',
    cta: '–°–º–æ—Ç—Ä–µ—Ç—å TikTok',
    link: 'https://www.tiktok.com/@neural_wolf',
    emoji: 'üéµ'
  },
  {
    title: 'YouTube –∫–∞–Ω–∞–ª',
    text: '–®–æ—Ä—Ç—Å—ã –∏ –¥–ª–∏–Ω–Ω—ã–µ –≤–∏–¥–æ—Å—ã —Å –≤–æ–ª–∫–∞–º–∏',
    cta: '–û—Ç–∫—Ä—ã—Ç—å YouTube',
    link: 'https://www.youtube.com/@neural_wolf',
    emoji: '‚ñ∂Ô∏è'
  }
];

const popupPayload = computed(() => socials[popupIndex.value % socials.length]);

const currentModeLabel = computed(() => {
  const m = modes.find((item) => item.id === mode.value);
  return m ? m.label : mode.value;
});

function resetAnimation() {
  isAnimating.value = false;
  requestAnimationFrame(() => {
    isAnimating.value = true;
  });
}

function getRandomItem(list, typeKey) {
  if (!list.length) return '';
  const currentLast = lastIndex.value[typeKey];
  let idx = Math.floor(Math.random() * list.length);
  if (list.length > 1 && idx === currentLast) {
    idx = (idx + 1) % list.length;
  }
  lastIndex.value[typeKey] = idx;
  return list[idx];
}

function pickHint() {
  const pool = hints[currentType.value];
  currentHint.value = getRandomItem(pool, currentType.value);
}

function ask(type) {
  currentType.value = type;
  const pool = questions[mode.value][type] || [];
  currentQuestion.value = getRandomItem(pool, type);
  pickHint();
  resetAnimation();
  triggerPopup();
}

function askTruth() {
  ask('truth');
}

function askDare() {
  ask('dare');
}

function askRandom() {
  const type = Math.random() < 0.5 ? 'truth' : 'dare';
  ask(type);
}

function startGame() {
  showIntro.value = false;
  askRandom();
}
function triggerPopup() {
  questionsSeen.value += 1;
  if (questionsSeen.value % 5 === 0) {
    popupIndex.value += 1;
    showPopup.value = true;
  }
}
</script>

